.md-ripple{width:100%;height:100%;position:relative;z-index:6;overflow:hidden;mask-image:radial-gradient(circle,0 0 0/100%,0 0 0/100%)}.md-ripple-wave{position:absolute;z-index:1;pointer-events:none;background:currentcolor;border-radius:50%}.md-ripple-wave.md-centered{animation-duration:1.2s;top:50%;left:50%}.md-ripple-wave~:not(.md-ripple-wave){position:relative;z-index:2}.md-ripple-wave.md-ripple-wave-enter{opacity:.1;transform:scale(.26) translateZ(0);transition:.8s cubic-bezier(.25, .8, .25, 1);transition-property:opacity,transform;will-change:opacity,transform}.md-ripple-wave.md-ripple-wave-enter.md-centered{transition-duration:1.2s}.md-ripple-wave,.md-ripple-wave.md-ripple-wave-enter-active{opacity:0;transform:scale(2) translateZ(0)}