.md-popover.md-menu>.content{background-color:var(--md-elevation-8);box-shadow:0 5px 5px -3px rgba(0,0,0,.2),0 8px 10px 1px rgba(0,0,0,.14),0 3px 14px 2px rgba(0,0,0,.12)}.md-menu{display:inline-block}.md-menu>.md-button{margin:0}.md-menu-content{max-height:35vh;display:flex;flex-direction:row}.md-menu-content.medium{min-width:168px}.md-menu-content.big{min-width:224px}.md-menu-content.huge{min-width:280px}.md-menu-content-container{flex:1;overflow:auto}.md-menu-content-container .md-list{transition:opacity .3s cubic-bezier(.25, .8, .25, 1);will-change:opacity;text-transform:none;white-space:nowrap}.md-menu-content-container .md-list .md-list-item-container{height:100%}@media (max-width:960px){.md-menu-content-container .md-list{font-size:14px}}