.md-overlay{position:absolute;top:0;right:0;bottom:0;left:0;z-index:101;overflow:hidden;background:rgba(0,0,0,.6)}.md-overlay.md-fixed,body>.md-overlay{position:fixed}.md-overlay-enter,.md-overlay-leave{transition:.35s cubic-bezier(.4, 0, .2, 1)}.md-overlay-enter.md-overlay-enter-active{opacity:1}.md-overlay-enter,.md-overlay-leave-active{opacity:0}