.md-icon{width:1em;height:1em;margin:auto;display:inline-flex;user-select:none;align-items:center;justify-content:center;vertical-align:middle;transition:color .4s cubic-bezier(.4, 0, .2, 1);direction:ltr;line-height:1;font-feature-settings:"liga";font-style:normal;letter-spacing:normal;text-rendering:optimizelegibility;text-transform:none;word-wrap:normal;white-space:nowrap;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.md-icon>svg{height:100%;flex:1;transition:fill .4s cubic-bezier(.4, 0, .2, 1);overflow:hidden;fill:currentcolor}