.md-drawer-container{position:relative;overflow:hidden}.md-drawer-container>.md-drawer-main{transition:margin .4s cubic-bezier(.4, 0, .2, 1)}.md-drawer{position:absolute;top:0;bottom:0;left:0;z-index:102;transform:translate3d(-100%,0,0);transition:transform .4s cubic-bezier(.4, 0, .2, 1);will-change:transform,box-shadow;width:400px;max-width:calc(100vw - 56px);overflow-x:hidden;overflow-y:auto;background:var(--md-background)}@media (max-width:600px){.md-drawer{width:320px}}.md-drawer.md-right{right:0;left:auto;transform:translate3d(100%,0,0)}.md-drawer.md-fixed{position:fixed}.md-drawer.md-active{transform:translate3d(0,0,0)}@media (max-width:600px){.md-drawer.md-active{background-color:var(--md-elevation-16);box-shadow:0 8px 10px -5px rgba(0,0,0,.2),0 16px 24px 2px rgba(0,0,0,.14),0 6px 30px 5px rgba(0,0,0,.12)}}.md-drawer .md-list-item-container{font-size:14px;text-transform:none}